















import { Vue, Component, Prop, Ref } from 'vue-property-decorator'
import Diagram, { Entity, EntityStyle, Field, Relation } from '@/diagram'
import cytoscape, { EdgeSingular, NodeSingular } from 'cytoscape'
import panzoom from 'cytoscape-panzoom'
import nodeHtmlLabel from 'cytoscape-node-html-label'
import CytoscapeContextMenu from 'cytoscape-cxtmenu'
import 'cytoscape-panzoom/cytoscape.js-panzoom.css'
import { unwatchRect, watchRect } from '@/util/watch-rect'
import iro from '@jaames/iro'
import { getPercBrightness } from '@/util/color'

const generateStyle = (diagram: Diagram): cytoscape.Stylesheet[] => {
  const printing = false

  const getStyle = (element: NodeSingular | EdgeSingular, property: string, defaultValue: any) => {
    const item = element.isNode() ? element.data('entity') : element.data('relation')
    return item.style[property] ?? item.type.style[property] ?? defaultValue
  }

  return [
    {
      selector: 'node',
      style: {
        'width': (node: cytoscape.NodeSingular) => node.data('width'),
        'height': (node: cytoscape.NodeSingular) => node.data('height'),
        'background-opacity': 0,
        'text-valign': 'center',
        'shape': 'rectangle',

        // 'display': node => (
        //   (!settings.people.active && node.data().entity.type === 'Person')
        //   || (!settings.unknown.active && node.data().entity.type === 'Unknown')
        //   || (!settings.foreign.active && node.data().entity.type === 'Foreign')
        //     ? 'none'
        //     : 'element'
        // ),
      },
    },

    {
      selector: 'edge',
      style: {
        'visibility': (edge: cytoscape.EdgeSingular) => edge.data('hidden') ? 'hidden' : 'visible',

        'width': () => 4,

        'label': (edge: cytoscape.EdgeSingular) => {
          const relation: Relation = edge.data('relation')

          const strings: string[] = []

          for (const fieldGroup of diagram.fieldGroups) {
            for (const field of fieldGroup.fields) {
              if (!field.isRelationLabel) continue
              if (!field.active) continue
              if (!field.relationTypes.has(relation.type)) continue

              // const value = relation.data[field.dataKey]
              const value = relation.getFieldValue(field)
              const formatted = String(field.formatter ? field.formatter(value) : value ?? '')
              if (formatted) strings.push(formatted)
            }
          }

          return strings.join('\n')
        },

        'color': () => printing ? '#000' : '#777',

        'curve-style': () => 'bezier',

        'text-background-color': () => '#fff',
        'text-background-opacity': () => 1,

        'line-color': () => printing ? '#999': '#d8d8d8',
        'source-arrow-color': () => printing ? '#999' : '#d8d8d8',
        'target-arrow-color': () => printing ? '#999' : '#d8d8d8',
        'source-arrow-fill': () => 'filled',
        'target-arrow-fill': () => 'filled',

        'line-style': edge => getStyle(edge, 'lineStyle', 'solid') === 'solid' ? 'solid' : 'dashed',
        "line-dash-pattern": ((edge: any) => getStyle(edge, 'lineStyle', 'solid') === 'dotted' ? [4, 8] : [8, 4]) as any,
        'source-arrow-shape': edge => getStyle(edge, 'arrowFrom', false) ? 'triangle' : 'none',
        'target-arrow-shape': edge => getStyle(edge, 'arrowTo', true) ? 'triangle' : 'none',

        'control-point-step-size': () => 100,
        'text-background-padding': () => '5px',

        // These don't exist in cytoscapes ts definitions
        ...{
          'loop-direction': () => '30deg',
          'loop-sweep': () => '45deg',
        },
      },
    },
  ]
}

const initNodeHtmlLabel = (graph: cytoscape.Core, diagram: Diagram) => {
  graph.nodeHtmlLabel([{
    query: '.entity:inside', // very important to include :inside, as it will otherwise render removed to the graph in some cases.
    tpl: data => {
      const entity: Entity = data.entity

      // FIXME: Disabled
      // // depending on the filters, we might not show the entity at all
      // if ((!settings.people.active && data.entity.type === 'Person') || (!settings.unknown.active && data.entity.type === 'Unknown') || (!settings.foreign.active && data.entity.type === 'Foreign')) return '<span></span>'

      const classes = [
        'lassox-diagram__Graph_entity',
        `lassox-diagram__Graph_entity--id-${data.id}`,
        entity.isMainEntity && 'lassox-diagram__Graph_entity--main-entity',
        data.selected && 'lassox-diagram__Graph_entity--selected',
      ].filter(v => !!v).join(' ')

      const createStyleString = (style: [string, unknown][]) => style.map(([prop, value], i) => {
        if (!value) return ''

        let string = ''
        if (i) string += ' '
        string += `${prop}: ${value};`
        return string
      }).join('')

      const getStyleValue = <P extends keyof EntityStyle> (prop: P) => entity.style[prop] ?? entity.type.style[prop]

      const getContrastingTextColor = (bgColor?: string) => getPercBrightness(new iro.Color(bgColor)) > 165
        ? 'rgba(0, 0, 0, 0.68)'
        : 'rgb(255, 255, 255)'

      const borderWidth = `${(getStyleValue('borderWidth') ?? 2)}px`
      const borderStyle = getStyleValue('borderStyle')
      const borderColor = entity.isMainEntity ? 'black' : getStyleValue('borderColor')
      const bgColor = getStyleValue('backgroundColor') ?? '#ffffff'
      const labelsBgColor = getStyleValue('labelsBackgroundColor') ?? bgColor

      const style = createStyleString([
        ['visibility', data.hidden ? 'hidden' : 'visible'],
        ['border-width', borderWidth],
        ['border-style', borderStyle],
        ['border-color', borderColor],
      ])

      const labelsStyle = createStyleString([
        ['color', getContrastingTextColor(labelsBgColor)],
        ['background-color', labelsBgColor],
      ])

      const fieldsStyle = createStyleString([
        ['border-top-width', borderWidth],
        ['border-style', borderStyle],
        ['border-color', borderColor],
        ['color', getContrastingTextColor(bgColor)],
        ['background-color', bgColor],
      ])

      const createHtmlField = (options: {
        field?: Field
        legendColor?: string
        text?: string
      }) => {
        const getFormattedFieldValue = (field: Field) => {
          const value = entity.getFieldValue(field)
          if (field.formatter) return field.formatter(value)
          return String(value ?? '')
        }

        const encodeHTML = (string: string) => string.replace(/&/g, '&amp;').replace(/</g, '&lt;').replace(/"/g, '&quot;')

        let legendColor = options.legendColor ?? (
          options.field?.addToLegend
            ? options.field.legendColor
            : null
        )

        let text = options.text ?? (
          options.field
            ? getFormattedFieldValue(options.field)
            : ''
        )

        if (!text) return ''

        let html = ''
        html += '<div class="lassox-diagram__Graph_entity-field">'
        if (legendColor) html += `<div class="lassox-diagram__Graph_entity-field-legend-color" style="background-color: ${legendColor};"></div>`
        html += `<div class="lassox-diagram__Graph_entity-field-value">${encodeHTML(text)}</div>`
        html += '</div>'
        return html
      }

      const labels: string[] = []
      const fields: string[] = []

      for (const fieldGroup of diagram.fieldGroups) {
        for (const field of fieldGroup.fields) {
          if (!field.active) continue
          if (!field.entityTypes.has(entity.type)) continue

          if (field.isEntityLabel) labels.push(createHtmlField({ field, legendColor: '' }))
          else fields.push(createHtmlField({ field }))
        }
      }

      if (!labels.length) {
        labels.push(createHtmlField({ text: entity.type.labels.singular }))
      }

      const labelsHtml = labels.join('')
      const fieldsHtml = fields.join('')

      return (
        `<div class="${classes}" style="${style}">` +
          `<div class="lassox-diagram__Graph_entity-labels-wrapper" style="${labelsStyle}">` +
            (entity.type.icon ? (
              `<div class="lassox-diagram__Graph_entity-icon material-icons-outlined">${entity.type.icon}</div>`
            ) : '') +

            '<div class="lassox-diagram__Graph_entity-labels">' +
              labelsHtml +
            '</div>' +
          '</div>' +

          (fieldsHtml ? (
            `<div class="lassox-diagram__Graph_entity-fields" style="${fieldsStyle}">` +
              fieldsHtml +
            '</div>'
          ) : '') +
        '</div>'
      )
    },
  }], {
    enablePointerEvents: true,
  })
}

panzoom(cytoscape)
nodeHtmlLabel(cytoscape)
cytoscape.use(CytoscapeContextMenu)

@Component({})

export default class Graph extends Vue {
  @Prop({ type: Diagram, required: true })
  readonly diagram!: Diagram

  @Ref()
  readonly graphContainerEl!: HTMLElement

  _graph?: cytoscape.Core

  addedItems = new Map<string, Entity | Relation>()

  activeDiagramChanged = true

  onDestroy?: () => void

  get el () {
    return this.$el as HTMLElement
  }

  mounted () {
    const destroyCallbacks: (() => void)[] = []

    destroyCallbacks.push(watchRect(this.el, this.onResize).unwatch)

    this.initialize()

    destroyCallbacks.push(this.diagram.eventBus.on(['dataUpdated', 'activeFieldsChanged'], () => this.updateElements()))
    destroyCallbacks.push(this.diagram.eventBus.on('activeLayoutChanged', () => this.runLayout(false)))

    this.$watch(() => this.diagram.activeDiagram, () => this.activeDiagramChanged = true)

    this.onDestroy = () => {
      for (const cb of destroyCallbacks) cb()
    }
  }

  beforeDestroy () {
    this.onDestroy?.()

    this._graph?.destroy()
    this._graph = undefined
  }

  // When clicking the graph container, cytoscape removes focus, so body ends up focused.
  // This focuses the graph container instead.
  redirectCytoscapeBlur (event: Event) {
    if (event.defaultPrevented) return

    setTimeout(() => {
      if (document.activeElement == null || document.activeElement == document.body) {
        (this.$el as HTMLElement).focus()
      }
    })
  }

  // Undo (ctrl+z) and redo (ctrl+y)
  onKeyDown (event: KeyboardEvent) {
    if (event.defaultPrevented) return

    if (event.ctrlKey) {
      const key = event.key.toLowerCase()

      const action = (
        key == 'z' ? 'undo' :
        key == 'y' ? 'redo' :
        null
      )

      if (action) {
        event.preventDefault()

        switch (action) {
          case 'undo': {
            this.diagram.activeDiagram.data.undo()
            break
          }

          case 'redo': {
            this.diagram.activeDiagram.data.redo()
            break
          }
        }
      }
    }
  }

  onResize () {
    if (this._graph) {
      const { width, height } = this.el.getBoundingClientRect()
      if (width === this._graph.width() && height === this._graph.height()) return
      this._graph.resize()
    }
  }

  initialize () {
    // Initialize cytoscape
    const graph = this._graph = cytoscape({
      container: this.graphContainerEl,
      style: generateStyle(this.diagram),
      wheelSensitivity: 0.05,
      maxZoom: 1,
    })

    // Initialize node-html-label
    initNodeHtmlLabel(graph, this.diagram)

    // Initialize panzoom
    graph.panzoom({
      zoomOnly: true,
      fitPadding: 32,
      zoomInIcon: 'material-icons-outlined',
      zoomOutIcon: 'material-icons-outlined',
      resetIcon: 'material-icons-outlined',
      zoomFactor: 0.1,
    })

    // Fit elements in graph when graph is resized
    graph.on('resize', () => {
      this.fitInGraph()
    })

    // Add inital elements
    this.updateElements()

    // Sometimes cytoscape will fail at initially fitting the elements in the graph, so we wait for a frame before doing it.
    // requestAnimationFrame(() => {
    //   this.fitInGraph()
    // })
  }

  updateElements () {
    const graph = this._graph
    if (!graph) return

    graph.batch(() => {
      const items = new Map<string, Entity | Relation>([
        ...this.diagram.activeDiagram.data.entitiesMap,
        ...this.diagram.activeDiagram.data.relationsMap,
      ])

      const toAdd: (Entity | Relation)[] = []
      const toUpdate: (Entity | Relation)[] = []
      const toRemove: (Entity | Relation)[] = []

      for (const [id, item] of items) {
        if (!this.addedItems.has(id)) toAdd.push(item)
        else toUpdate.push(item)
      }

      for (const [id, item] of this.addedItems) {
        if (!items.has(id)) toRemove.push(item)
      }

      const elementDefinitions: cytoscape.ElementDefinition[] = []

      for (const item of toAdd) {
        if (item.isEntity) {
          elementDefinitions.push({
            group: 'nodes',
            classes: 'entity',
            data: {
              id: item.id,
              entity: item,
              width: 160,
              height: 36,
            },
          })
        } else {
          elementDefinitions.push({
            group: 'edges',
            classes: 'relation',
            data: {
              id: item.id,
              source: item.from,
              target: item.to,
              relation: item,
            },
          })
        }
      }

      for (const item of toUpdate) {
        graph.$id(item.id).data(
          item.isEntity
            ? { entity: item }
            : { relation: item }
        )
      }

      for (const item of toRemove) {
        graph.$id(item.id).remove()
      }

      const animateLayout = !!graph.elements().length

      const elements = graph.add(elementDefinitions)
      const nodes = elements.nodes()
      const edges = elements.edges()

      elements.data('hidden', true)

      this.addedItems = items

      if (graph.elements().length) {
        const runLayout = this.activeDiagramChanged
        this.activeDiagramChanged = false

        graph.one('render', () => {
          graph.batch(() => {
            nodes.forEach(node => {
              const nodeEl = this.el.querySelector(`.lassox-diagram__Graph_entity--id-${node.id()}`)?.parentElement
              if (!nodeEl) return

              const onRectChanged = (rect: DOMRect, oldRect: DOMRect | null) => {
                if (oldRect && rect.width === oldRect.width && rect.height === oldRect.height) return

                const zoom = this._graph?.zoom() ?? 1
                const width = rect.width / zoom
                const height = rect.height / zoom

                const { width: oldWidth, height: oldHeight } = node.data()

                if (width === oldWidth && height === oldHeight) return

                node.data({
                  width,
                  height,
                })
              }

              watchRect(nodeEl, onRectChanged)

              node.on('remove', () => {
                unwatchRect(nodeEl, onRectChanged)
              })
            })
          })

          if (!runLayout) {
            graph.one('render', () => {
              elements.data('hidden', false)
            })
          } else {
            graph.one('layoutstart', () => {
              elements.data('hidden', false)
            })

            this.runLayout(animateLayout)
          }
        })

        nodes.on('click', event => {
          this.$emit('clickEntity', event.target.data('entity'))
        })

        edges.on('click', event => {
          this.$emit('clickRelation', event.target.data('relation'))
        })

        elements.on('select', event => {
          event.target.data('selected', true)
          if (event.target.isNode()) this.$emit('selectEntity', event.target.data('entity'))
          else if (event.target.isEdge()) this.$emit('selectRelation', event.target.data('relation'))
        })

        elements.on('unselect', event => {
          event.target.removeData('selected')
          if (event.target.isNode()) this.$emit('unselectEntity', event.target.data('entity'))
          else if (event.target.isEdge()) this.$emit('unselectRelation', event.target.data('relation'))
        })
      }
    })
  }

  runLayout (animate = true) {
    const graph = this._graph
    if (!graph) return

    const layout = this.diagram.activeDiagram.settings.activeLayout

    return new Promise<void>(resolve => {
      graph.layout({
        name: 'breadthfirst',
        fit: true,
        padding: 32,
        directed: layout?.directed ?? false,
        grid: layout?.grid ?? false,
        spacingFactor: layout?.spacingFactor ?? 1.3,
        maximal: layout?.maximal ?? false,
        avoidOverlap: true,
        animate: animate && (layout?.animate ?? true),
        animationDuration: layout?.animationDuration ?? 1000,
        animationEasing: layout?.animationEasing ?? 'ease-out' as any,
        transform: layout?.inverted
          ? (_: any, pos: cytoscape.Position): cytoscape.Position => ({ x: -pos.x, y: -pos.y })
          : undefined,
        stop: () => resolve(),
      }).run()
    })
  }

  fitInGraph () {
    this._graph?.fit(undefined, 32)
  }
}
