






























































































































































































import { Vue, Component, Prop, Watch, Inject } from 'vue-property-decorator'
import Diagram, { Entity, Relation, RelationType } from '@/diagram'
import IconButton from './IconButton.vue'
import ButtonGroup from './ButtonGroup.vue'
import Button from './Button.vue'
import DropdownMenu from './DropdownMenu.vue'
import DropdownMenuItem from './DropdownMenuItem.vue'
import DropdownMenuDivider from './DropdownMenuDivider.vue'
import FieldContainer from './FieldContainer.vue'
import TextField from './TextField.vue'
import RadioField from './RadioField.vue'
import ColorField from './ColorField.vue'
import _set from 'lodash.set'
import DiagramVue from './Diagram.vue'
import ConfirmAdditionDialog from './ConfirmAdditionDialog.vue'
import { DiagramDataDefinition } from '@/types/DiagramData'

const filterOutExistingData = (diagram: Diagram, data: DiagramDataDefinition): DiagramDataDefinition => {
  const activeData = diagram.activeDiagram.data
  return {
    entities: data.entities?.filter(e => !activeData.entitiesMap.has(e.id)),
    relations: data.relations?.filter(r => !activeData.relationsMap.has(r.id)),
  }
}

interface AddMenuItem {
  key: string
  label: string
  onClick: () => void
}

interface EditorFieldGroup {
  id: string
  title: string
  fields: EditorField[]
}

type EditorField = (
  | EditorTextField
  | EditorNumberField
  | EditorRadioField
  | EditorColorField
)

interface EditorBaseField {
  id: string
  title: string
  dataKey?: string
  styleKey?: string
  initialValue?: any
  initialValueLabel?: string
  resetValue?: any
  value: any
}

interface EditorTextField extends EditorBaseField {
  type: 'text'
}

interface EditorNumberField extends EditorBaseField {
  type: 'number'
}

interface EditorRadioField extends EditorBaseField {
  type: 'radio-buttons'
  options: EditorRadioFieldOption[]
}

interface EditorRadioFieldOption {
  label: string
  value: any
}

interface EditorColorField extends EditorBaseField {
  type: 'color'
}

@Component({
  components: {
    IconButton,
    ButtonGroup,
    Button,
    DropdownMenu,
    DropdownMenuItem,
    DropdownMenuDivider,
    FieldContainer,
    TextField,
    RadioField,
    ColorField,
  },
})

export default class Editor extends Vue {
  @Inject()
  readonly diagramVm!: DiagramVue

  @Prop({ type: Diagram, required: true })
  readonly diagram!: Diagram

  @Prop({ type: [ Entity, Relation ], required: true })
  readonly object!: Entity | Relation

  @Prop({ type: Function, required: true })
  readonly close!: () => void

  showAddDropdownMenu = false

  fieldGroups: EditorFieldGroup[] = []

  get label () {
    return this.object.type.labels.editorLabel?.(this.object.data) ?? this.object.type.labels.singular.toLowerCase()
  }

  get addMenuItems () {
    const entity = this.object
    if (!entity.isEntity) return null

    const items = {
      new: [] as AddMenuItem[],
      existing: [] as AddMenuItem[],
    }

    const addItem = (options: {
      type: RelationType
      supportsParent: boolean
      supportsChild: boolean
    }) => {
      const { type, supportsParent, supportsChild } = options

      if (supportsParent) {
        // items.new.push({
        //   key: `new-${type.id}-parent`,
        //   label: type.labels.newParent ?? 'Ny(t)' + (type.labels.singularFrom || type.labels.singular).toLowerCase(),
        //   onClick: () => {},
        // })

        items.existing.push({
          key: `existing-${type.id}-parents`,
          label: type.labels.existingParents ?? 'Eksisterende ' + (type.labels.pluralFrom || type.labels.plural).toLowerCase(),
          onClick: () => {
            this.diagram.methods.getEntityData?.({
              ids: [entity.id],
              relationTypes: [type.id],
              parents: 1,
              children: 0,
            }).then(data => {
              data = filterOutExistingData(this.diagram, data)

              if (data.entities?.length || data.relations?.length) {
                this.diagramVm.showDialog(ConfirmAdditionDialog, {
                  data: data,
                  onConfirm: () => this.diagram.activeDiagram.data.add(data),
                })
              } else {
                // TODO: Toast?
              }
            })
          },
        })
      }

      if (supportsChild) {
        // items.new.push({
        //   key: `new-${type.id}-child`,
        //   label: type.labels.newChild ?? 'Ny(t)' + (type.labels.singularTo || type.labels.singular).toLowerCase(),
        //   onClick: () => {},
        // })

        items.existing.push({
          key: `existing-${type.id}-children`,
          label: type.labels.existingChildren ?? 'Eksisterende ' + (type.labels.pluralTo || type.labels.plural).toLowerCase(),
          onClick: () => {
            this.diagram.methods.getEntityData?.({
              ids: [entity.id],
              relationTypes: [type.id],
              parents: 0,
              children: 1,
            }).then(data => {
              data = filterOutExistingData(this.diagram, data)

              if (data.entities?.length || data.relations?.length) {
                this.diagramVm.showDialog(ConfirmAdditionDialog, {
                  data: data,
                  onConfirm: () => this.diagram.activeDiagram.data.add(data),
                })
              } else {
                // TODO: Toast?
              }
            })
          },
        })
      }
    }

    for (const type of this.diagram.relationTypes.items) {
      let supportsParent = false
      let supportsChild = false

      for (const support of type.supports) {
        if (supportsParent && supportsChild) break
        if (support.to == entity.type.id) supportsParent = true
        if (support.from == entity.type.id) supportsChild = true
      }

      addItem({ type, supportsParent, supportsChild })
    }

    if (!items.new.length && !items.existing.length) return null

    return [
      items.new,
      items.existing,
    ].filter(items => items.length)
  }

  get changedFieldGroups () {
    return this.fieldGroups
      .map(fieldGroup => ({
        ...fieldGroup,
        fields: fieldGroup.fields.filter(field => field.value != field.initialValue),
      }))
      .filter(fieldGroup => fieldGroup.fields.length)
  }

  get hasChanges () {
    return !!this.changedFieldGroups.length
  }

  remove () {
    if (this.object.isEntity) {
      const entityId = this.object.id

      const connectedRelationIds = (
        this.diagram.activeDiagram.data.relations
          .filter(relation => relation.from == entityId || relation.to == entityId)
          .map(relation => relation.id)
      )

      this.diagram.activeDiagram.data.remove({
        entityIds: [entityId],
        relationIds: connectedRelationIds,
      })
    } else {
      this.diagram.activeDiagram.data.remove({
        relationIds: [this.object.id],
      })
    }

    this.close()
  }

  saveChanges () {
    const id = this.object.id

    const newData: Record<string, any> = {}
    const newStyle: Record<string, any> = {}

    for (const fieldGroup of this.changedFieldGroups) {
      for (const field of fieldGroup.fields) {
        if (field.dataKey) _set(newData, field.dataKey, field.value)
        else if (field.styleKey) _set(newStyle, field.styleKey, field.value)
      }
    }

    if (this.object.isEntity) {
      this.diagram.activeDiagram.data.commitChange({
        type: 'edit',
        entities: [{ id, data: newData, style: newStyle }],
      })
    } else {
      this.diagram.activeDiagram.data.commitChange({
        type: 'edit',
        relations: [{ id, data: newData, style: newStyle }],
      })
    }

    this.close()
  }

  @Watch('object', { immediate: true })
  onObjectChanged () {
    const { diagram, object } = this

    const createField = (options: EditorBaseField): EditorBaseField => ({
      id: options.id,
      title: options.title,
      dataKey: options.dataKey,
      styleKey: options.styleKey,
      initialValue: options.initialValue ?? options.value,
      initialValueLabel: options.initialValueLabel ?? `${options.value ?? ''}`,
      value: options.value,
      resetValue: options.resetValue,
    })

    const createTextField = (options: Omit<EditorTextField, 'type'>): EditorTextField => ({
      ...createField(options),
      type: 'text',
    })

    const createNumberField = (options: Omit<EditorNumberField, 'type'>): EditorNumberField => ({
      ...createField(options),
      type: 'number',
    })

    const createRadioField = (options: Omit<EditorRadioField, 'type' | 'selectedOption' | 'initialSelectedOption'>): EditorRadioField => ({
      ...createField(options),
      type: 'radio-buttons',
      options: options.options,
      initialValueLabel: options.initialValueLabel ?? options.options.find(o => o.value === options.value)?.label,
    })

    const createBooleanField = (options: Omit<EditorBaseField, 'type'>): EditorRadioField => createRadioField({
      ...options,
      options: [
        {
          label: 'Ja',
          value: true,
        },
        {
          label: 'Nej',
          value: false,
        },
      ],
    })

    const createColorField = (options: Omit<EditorColorField, 'type'>): EditorColorField => ({
      ...createField(options),
      type: 'color',
    })

    const fieldGroups: EditorFieldGroup[] = []

    for (const fieldGroup of diagram.fieldGroups) {
      const fields: EditorField[] = []

      for (const field of fieldGroup.fields) {
        if (!field.active) continue
        if (object.isEntity && !field.entityTypes.has(object.type)) continue
        if (object.isRelation && !field.relationTypes.has(object.type)) continue

        const baseField: EditorBaseField = {
          id: field.fullId,
          title: field.title,
          dataKey: field.dataKey,
          value: object.getFieldValue(field),
        }

        let newField: EditorField

        switch (field.type) {
          case 'text': {
            newField = createTextField(baseField)
            break
          }

          case 'boolean': {
            newField = createBooleanField(baseField)
            break
          }

          case 'radio-buttons': {
            // TODO
            newField = createRadioField({
              ...baseField,
              options: [],
            })
            break
          }
        }

        fields.push(newField)
      }

      if (!fields.length) continue

      fieldGroups.push({
        id: `fg-${fieldGroup.id}`,
        title: fieldGroup.title,
        fields: fields,
      })
    }

    const styleFields: EditorField[] = []

    function getStyleValues<S> (object: { style: S, type: { style: S } }, prop: keyof S) {
      return {
        value: object.style[prop] ?? object.type.style[prop],
        resetValue: object.type.style[prop],
      }
    }

    if (object.isEntity) {
      styleFields.push(
        createColorField({
          id: 'labelsBackgroundColor',
          title: 'Top baggrundsfarve',
          styleKey: 'labelsBackgroundColor',
          ...getStyleValues(object, 'labelsBackgroundColor'),
        }),
        createColorField({
          id: 'backgroundColor',
          title: 'Baggrundsfarve',
          styleKey: 'backgroundColor',
          ...getStyleValues(object, 'backgroundColor'),
        }),
        createColorField({
          id: 'borderColor',
          title: 'Kantfarve',
          styleKey: 'borderColor',
          ...getStyleValues(object, 'borderColor'),
        }),
        createRadioField({
          id: 'borderStyle',
          title: 'Kanttype',
          styleKey: 'borderStyle',
          options: [
            {
              label: 'Solid',
              value: 'solid',
            },
            {
              label: 'Prikket',
              value: 'dotted',
            },
            {
              label: 'Stiplet',
              value: 'dashed',
            },
          ],
          ...getStyleValues(object, 'borderStyle'),
        }),
        createNumberField({
          id: 'borderWidth',
          title: 'Kantbredde',
          styleKey: 'borderWidth',
          ...getStyleValues(object, 'borderWidth'),
        }),
      )
    } else {
      styleFields.push(
        createBooleanField({
          id: 'arrowFrom',
          title: 'Vis pil fra',
          styleKey: 'arrowFrom',
          ...getStyleValues(object, 'arrowFrom'),
        }),
        createBooleanField({
          id: 'arrowTo',
          title: 'Vis pil til',
          styleKey: 'arrowTo',
          ...getStyleValues(object, 'arrowTo'),
        }),
        createRadioField({
          id: 'lineStyle',
          title: 'Linjetype',
          styleKey: 'lineStyle',
          options: [
            {
              label: 'Solid',
              value: 'solid',
            },
            {
              label: 'Prikket',
              value: 'dotted',
            },
            {
              label: 'Stiplet',
              value: 'dashed',
            },
          ],
          ...getStyleValues(object, 'lineStyle'),
        }),
      )
    }

    if (styleFields.length) fieldGroups.push({
      id: 'style',
      title: 'Udseende',
      fields: styleFields,
    })

    for (const fieldGroup of fieldGroups) {
      for (const field of fieldGroup.fields) {
        field.initialValue = field.value
      }
    }

    this.fieldGroups = fieldGroups
  }
}
