




































































import { Vue, Component, Prop } from 'vue-property-decorator'
import IconButton from './IconButton.vue'

@Component({
  components: {
    IconButton,
  },
})

export default class Dialog extends Vue {
  @Prop(Boolean)
  dismissable?: boolean

  @Prop(String)
  title?: string

  @Prop(String)
  content?: string

  @Prop(Function)
  onClose?: () => void

  close () {
    this.$emit('close')
    this.onClose?.()
  }

  get slotProps () {
    return {
      close: this.close,
    }
  }
}
