import Vue from 'vue'
import DiagramEventBus from './types/DiagramEventBus'
import DiagramVue from './components/Diagram.vue'
import DiagramData, { DiagramDataDefinition } from './types/DiagramData'
import { getDeep, setDeep } from './util/get-deep-property'
import cytoscape from 'cytoscape'

export default class Diagram {
  static init (config: DiagramConfig) {
    return new Diagram(config)
  }

  container?: HTMLElement
  element?: Element
  vm?: Vue

  eventBus = new DiagramEventBus()

  mainEntityId: string

  entityTypes: EntityTypes
  relationTypes: RelationTypes

  fieldGroups: FieldGroup[]
  fields: Field[]
  filters: Filter[]

  layouts: LayoutDefinition[]

  methods: {
    getEntityData?: (options: {
      ids: string[]
      relationTypes: string[]
      parents: number
      children: number
    }) => Promise<DiagramDataDefinition>
  }

  saveSettingsToLocalStorage: boolean

  activeDiagram: ActiveDiagram

  constructor (config: DiagramConfig) {
    this.mainEntityId = config.mainEntityId

    this.entityTypes = new EntityTypes(config.entityTypes ?? [])
    this.relationTypes = new RelationTypes(config.relationTypes ?? [])

    this.fieldGroups = config.fieldGroups?.map(f => new FieldGroup(this, f)) ?? []
    this.fields = this.fieldGroups.flatMap(fieldGroup => fieldGroup.fields)
    this.filters = config.filters?.map(f => new Filter(this, f)) ?? []

    this.layouts = config.layouts ?? []

    this.methods = {
      getEntityData: config.methods?.getEntityData,
    }

    this.saveSettingsToLocalStorage = config.saveSettingsToLocalStorage ?? false

    this.activeDiagram = new ActiveDiagram(this)

    this.methods.getEntityData?.({
      ids: [this.mainEntityId],
      relationTypes: this.relationTypes.items.map(rt => rt.id),
      parents: 10,
      children: 10,
    })
      .then(data => {
        this.activeDiagram = new ActiveDiagram(this, data)
        this.eventBus.emit({ name: 'dataUpdated' })
      })
  }

  render (container: Element) {
    if (this.element) this.element.parentElement?.removeChild(this.element)
    if (this.vm) this.vm.$destroy()

    const element = document.createElement('div')
    container.appendChild(element)

    this.element = element
    this.vm = new Vue({
      el: element,
      render: h => h(DiagramVue, {
        props: {
          diagram: Vue.observable(this),
        },
      }),
    })

    return element
  }
}

export interface DiagramConfig {
  mainEntityId: string
  entityTypes?: EntityTypeDefinition[]
  relationTypes?: RelationTypeDefinition[]
  fieldGroups?: FieldGroupDefinition[]
  filters?: FilterDefinition[]
  layouts?: LayoutDefinition[]
  methods?: {
    onBeforePrint?: () => void
    onAfterPrint?: () => void
    getSavedDiagrams?: () => Promise<unknown>
    saveDiagram?: (savedDiagram: unknown) => Promise<void>
    deleteSavedDiagram?: (savedDiagram: unknown) => Promise<void>
    downloadPdf?: (blob: Blob) => Promise<void>
    getEntityData?: (options: {
      ids: string[], // Entity ids
      relationTypes: string[], // RelationType ids
      parents: number,
      children: number,
    }) => Promise<DiagramDataDefinition>
  }
  savedDiagram?: unknown[]
  enableSaving?: boolean
  enableSharing?: boolean
  enableEditing?: boolean
  saveSettingsToLocalStorage?: boolean
}

export class ActiveDiagram {
  constructor (diagram: Diagram, data?: DiagramDataDefinition) {
    this.diagram = diagram
    this.data = new DiagramData(diagram, { data })
    this.settings = new Settings(diagram)
  }

  diagram: Diagram

  id = ''
  title = ''
  description = ''
  shared = false

  data: DiagramData
  settings: Settings

  async expand () {
    const data = await this.diagram.methods.getEntityData?.({
      ids: this.data.entities.map(e => e.id),
      relationTypes: this.diagram.relationTypes.items.map(rt => rt.id),
      parents: 1,
      children: 1,
    })

    if (!data) return

    this.data.add(data)
  }

  setData (data: DiagramDataDefinition) {
    this.data = new DiagramData(this.diagram, { data })
  }
}

export class EntityTypes {
  constructor (items: EntityTypeDefinition[]) {
    this.items = items.map(item => new EntityType(item))
    this.itemsMap = new Map(this.items.map(item => [item.id, item]))
  }

  items: EntityType[]
  itemsMap: Map<string, EntityType>

  has (idOritem: string | EntityType) {
    return typeof idOritem == 'string'
      ? this.itemsMap.has(idOritem)
      : this.itemsMap.has(idOritem.id)
  }

  get (id: string) {
    const item = this.itemsMap.get(id)
    if (!item) throw Error(`No entity type with id "${id}"`)
    return item
  }

  getMultiple (ids: string[]) {
    return ids.map(id => this.get(id))
  }

  pick (ids: string[]) {
    return new EntityTypes(this.getMultiple(ids))
  }
}

export class RelationTypes {
  constructor (items: RelationTypeDefinition[]) {
    this.items = items.map(item => new RelationType(item))
    this.itemsMap = new Map(this.items.map(item => [item.id, item]))
  }

  items: RelationType[]
  itemsMap: Map<string, RelationType>

  has (idOritem: string | RelationType) {
    return typeof idOritem == 'string'
      ? this.itemsMap.has(idOritem)
      : this.itemsMap.has(idOritem.id)
  }

  get (id: string) {
    const item = this.itemsMap.get(id)
    if (!item) throw Error(`No relation type with id "${id}"`)
    return item
  }

  getMultiple (ids: string[]) {
    return ids.map(id => this.get(id))
  }

  pick (ids: string[]) {
    return new RelationTypes(this.getMultiple(ids))
  }
}

export class EntityType {
  id: string
  icon?: string
  labels: {
    singular: string
    plural: string
    editorLabel?: (data: Record<string, any>) => string
  }
  style: EntityStyle

  constructor (options: EntityTypeDefinition) {
    this.id = options.id
    this.icon = options.icon ?? undefined
    this.labels = options.labels
    this.style = {
      backgroundColor: options.style?.backgroundColor ?? '#ffffff',
      borderColor: options.style?.borderColor ?? '#e0e0e0',
      borderStyle: options.style?.borderStyle ?? 'solid',
      borderWidth: options.style?.borderWidth ?? 2,
      labelsBackgroundColor: options.style?.labelsBackgroundColor ?? '#ffffff',
    }
  }
}

export interface EntityTypeDefinition {
  id: string
  icon?: string
  labels: {
    singular: string
    plural: string
    editorLabel?: (data: Record<string, any>) => string
  }
  style?: EntityStyle
}

export class RelationType {
  id: string
  labels: {
    singular: string
    plural: string
    newParent?: string
    newChild?: string
    existingParents?: string
    existingChildren?: string
    singularFrom?: string
    pluralFrom?: string
    singularTo?: string
    pluralTo?: string
    editorLabel?: (data: Record<string, any>) => string
  }
  style: RelationStyle
  supports: RelationTypeSupport[]

  constructor (options: RelationTypeDefinition) {
    this.id = options.id
    this.labels = options.labels
    this.style = {
      arrowFrom: options.style?.arrowFrom ?? false,
      arrowTo: options.style?.arrowTo ?? true,
      lineStyle: options.style?.lineStyle ?? 'solid',
    }
    this.supports = options.supports.map(r => new RelationTypeSupport(r))
  }
}

export type RelationTypeDefinition = {
  id: string
  labels: {
    singular: string
    plural: string
    newParent?: string
    newChild?: string
    existingParents?: string
    existingChildren?: string
    singularFrom?: string
    pluralFrom?: string
    singularTo?: string
    pluralTo?: string
    editorLabel?: (data: Record<string, any>) => string
  }
  style?: RelationStyle
  supports: RelationTypeSupportDefinition[]
}

export class RelationTypeSupport {
  from: string
  to: string
  allowAddExisting: boolean

  constructor (options: RelationTypeSupportDefinition) {
    this.from = options.from
    this.to = options.to
    this.allowAddExisting = options.allowAddExisting ?? true
  }
}

export interface RelationTypeSupportDefinition {
  from: string
  to: string
  allowAddExisting?: boolean
}

export class Entity {
  diagram: Diagram

  readonly isEntity = true
  readonly isRelation = false

  type: EntityType
  id: string
  data: Record<string, any>
  position: {
    x: number
    y: number
  } | null
  style: EntityStyle

  constructor (diagram: Diagram, options: EntityDefinition) {
    this.diagram = diagram

    this.type = diagram.entityTypes.get(options.type)
    this.id = options.id
    this.position = options.position ? { ...options.position } : null
    this.data = { ...options.data ?? {} }
    this.style = { ...options.style ?? {} }
  }

  get isMainEntity () {
    return this.id == this.diagram.mainEntityId
  }

  getFieldValue (field: Field) {
    let value = getDeep(this.data, field.dataKey)

    if (value === undefined || value === null) {
      value = field.getInitialValue?.(this.data)
      setDeep(this.data, field.dataKey, value)
    }

    return value
  }
}

export interface EntityDefinition {
  type: string
  id: string
  data?: Record<string, any>
  position?: {
    x: number
    y: number
  }
  style?: EntityStyle
}

export interface EntityStyle {
  backgroundColor?: string
  borderColor?: string
  borderStyle?: 'solid' | 'dotted' | 'dashed'
  borderWidth?: number
  labelsBackgroundColor?: string
}

export class Relation {
  diagram: Diagram

  readonly isEntity = false
  readonly isRelation = true

  type: RelationType
  id: string
  from: string
  to: string
  data: Record<string, any>
  style: RelationStyle

  constructor (diagram: Diagram, options: RelationDefinition) {
    this.diagram = diagram

    this.type = diagram.relationTypes.get(options.type)
    this.id = options.id
    this.from = options.from
    this.to = options.to
    this.data = { ...options.data ?? {} }
    this.style = { ...options.style ?? {} }
  }

  getFieldValue (field: Field) {
    let value = getDeep(this.data, field.dataKey)

    if (value === undefined || value === null) {
      value = field.getInitialValue?.(this.data)
      setDeep(this.data, field.dataKey, value)
    }

    return value
  }
}

export interface RelationDefinition {
  type: string
  id: string
  from: string
  to: string
  data?: Record<string, any>
  style?: RelationStyle
}

export interface RelationStyle {
  arrowFrom?: boolean
  arrowTo?: boolean
  lineStyle?: 'solid' | 'dotted' | 'dashed'
}

export class FieldGroup {
  id: string
  title: string
  fields: Field[]

  constructor (diagram: Diagram, options: FieldGroupDefinition) {
    this.id = options.id
    this.title = options.title
    this.fields = options.fields.map(f => new Field(diagram, this, f))
  }
}

export interface FieldGroupDefinition {
  id: string
  title: string
  fields: FieldDefinition[]
}

export class Field {
  diagram: Diagram
  fieldGroup: FieldGroup

  id: string
  fullId: string
  type: 'text' | 'boolean' | 'radio-buttons'
  title: string
  entityTypes: EntityTypes
  relationTypes: RelationTypes
  dataKey: string
  getInitialValue?: (data: Record<string, any>) => any
  formatter?: (value: any) => any
  isEntityLabel: boolean
  isRelationLabel: boolean
  addToLegend: boolean
  legendColor: string | null
  startActive: boolean
  display: boolean

  constructor (
    diagram: Diagram,
    fieldGroup: FieldGroup,
    options: FieldDefinition,
  ) {
    this.diagram = diagram
    this.fieldGroup = fieldGroup

    this.id = options.id
    this.fullId = `${fieldGroup.id}.${this.id}`
    this.type = options.type ?? 'text'
    this.title = options.title
    this.entityTypes = diagram.entityTypes.pick(options.entityTypes ?? [])
    this.relationTypes = diagram.relationTypes.pick(options.relationTypes ?? [])
    this.dataKey = options.dataKey
    this.getInitialValue = options.getInitialValue
    this.formatter = options.formatter
    this.isEntityLabel = options.isEntityLabel ?? false
    this.isRelationLabel = options.isRelationLabel ?? false
    this.addToLegend = options.addToLegend ?? false
    this.legendColor = options.legendColor ?? null
    this.startActive = options.startActive ?? false
    this.display = options.display ?? true
  }

  get active () {
    return this.diagram.activeDiagram.settings.activeFields[this.fullId] ?? this.startActive
  }

  set active (active) {
    if (this.diagram.activeDiagram.settings.activeFields[this.fullId] === active) return

    this.diagram.activeDiagram.settings.activeFields[this.fullId] = active
    this.diagram.eventBus.emit({ name: 'settingsChanged' })
    this.diagram.eventBus.emit({ name: 'activeFieldsChanged' })
  }
}

export interface FieldDefinition {
  id: string
  type?: 'text' | 'boolean'
  title: string
  entityTypes?: string[]
  relationTypes?: string[]
  dataKey: string
  getInitialValue?: (data: Record<string, any>) => any
  formatter?: (value: any) => any
  isEntityLabel?: boolean
  isRelationLabel?: boolean
  addToLegend?: boolean
  legendColor?: string
  startActive?: boolean
  display?: boolean
}

export class Filter {
  diagram: Diagram

  id: string
  title: string
  type: string | null
  filter: (entity: any) => boolean
  startActive: boolean

  constructor (diagram: Diagram, options: FilterDefinition) {
    this.diagram = diagram

    this.id = options.id
    this.title = options.title
    this.type = options.type ?? null
    this.filter = options.filter ?? (() => true)
    this.startActive = options.startActive ?? false
  }

  get active () {
    return this.diagram.activeDiagram.settings.activeFilters[this.id] ?? this.startActive
  }

  set active (active) {
    if (this.diagram.activeDiagram.settings.activeFilters[this.id] === active) return

    this.diagram.activeDiagram.settings.activeFilters[this.id] = active
    this.diagram.eventBus.emit({ name: 'settingsChanged' })
    this.diagram.eventBus.emit({ name: 'activeFiltersChanged' })
  }
}

export interface FilterDefinition {
  id: string
  title: string
  type?: string
  filter?: (entity: any) => boolean
  startActive?: boolean
}

export interface LayoutDefinition {
  id: string
  name: string // The name of the layout
  default?: boolean
  directed?: boolean // whether the tree is directed downwards (or edges can point in any direction if false)
  inverted?: boolean
  grid?: boolean // whether to create an even grid into which the DAG is placed (circle:false only)
  spacingFactor?: number // positive spacing factor, larger => more space between nodes (N.B. n/a if causes overlap)
  maximal?: boolean // whether to shift nodes down their natural BFS depths in order to avoid upwards edges (DAGS only)
  animate?: boolean // whether to transition the node positions
  animationDuration?: number // duration of animation in ms if enabled
  animationEasing?: string // easing of animation if enabled,
}

export class Settings {
  diagram: Diagram

  activeFields: Record<string, boolean | undefined> = {}
  activeFilters: Record<string, boolean | undefined> = {}
  activeLayout: LayoutDefinition | null = null

  constructor (diagram: Diagram) {
    this.diagram = diagram
    this.reset()

    this.diagram.eventBus.on('settingsChanged', () => {
      this.saveToLocalStorage()
    })
  }

  reset () {
    this.activeFields = {}
    this.activeFilters = {}

    for (const field of this.diagram.fields) {
      this.activeFields[field.fullId] = field.startActive
    }

    for (const filter of this.diagram.filters) {
      this.activeFilters[filter.id] = filter.startActive
    }

    this.activeLayout = this.diagram.layouts.find(l => l.default) ?? this.diagram.layouts[0] ?? null

    this.loadFromLocalStorage()
    this.saveToLocalStorage()
  }

  loadFromLocalStorage () {
    if (!this.diagram.saveSettingsToLocalStorage) return

    try {
      const json = localStorage.getItem('lassox-diagram/settings')
      if (!json) return

      const settings = JSON.parse(json)
      if (!isRecord(settings)) return

      const { activeFields, activeFilters, activeLayout } = settings

      if (isRecord(activeFields)) {
        for (const field of this.diagram.fields) {
          const active = activeFields[field.fullId]
          if (typeof active == 'boolean') this.activeFields[field.fullId] = active
        }
      }

      if (isRecord(activeFilters)) {
        for (const filter of this.diagram.filters) {
          const active = activeFilters[filter.id]
          if (typeof active == 'boolean') this.activeFilters[filter.id] = active
        }
      }

      if (typeof activeLayout === 'string') {
        const layout = this.diagram.layouts.find(l => l.id == activeLayout)
        if (layout) this.activeLayout = layout
      }
    } catch {
      return
    }
  }

  saveToLocalStorage () {
    if (!this.diagram.saveSettingsToLocalStorage) return

    try {
      localStorage.setItem('lassox-diagram/settings', JSON.stringify({
        activeFields: this.activeFields,
        activeFilters: this.activeFilters,
        activeLayout: this.activeLayout?.id,
      }))
    } catch {
      return
    }
  }
}

const isRecord = <K extends string = string> (v: unknown): v is Record<K, unknown> => typeof v == 'object' && v !== null

export * from './example'
