import Diagram, { Entity, EntityDefinition, EntityStyle, Relation, RelationDefinition, RelationStyle } from '@/diagram'
import { merge } from '@/util/revertible-object-manipulation'

export default class DiagramData {
  constructor (diagram: Diagram, options?: {
    data?: DiagramDataDefinition
    changes?: Change[]
  }) {
    this.diagram = diagram

    if (options) {
      this.initialData = {
        entities: options.data?.entities?.map(item => new Entity(this.diagram, item)) ?? [],
        relations: options.data?.relations?.map(item => new Relation(this.diagram, item)) ?? [],
        changes: options.changes ?? [],
      }

      this.entitiesMap.clear()
      this._addEntities(this.initialData.entities)

      this.relationsMap.clear()
      this._addRelations(this.initialData.relations)

      for (const change of this.initialData.changes) {
        this.commitChange(change, true)
      }

      this.diagram.eventBus.emit({ name: 'dataUpdated' })
    }
  }

  diagram: Diagram

  initialData: {
    entities: Entity[]
    relations: Relation[]
    changes: Change[]
  } = {
    entities: [],
    relations: [],
    changes: [],
  }

  changes: CompiledChange[] = []
  undoneChanges: CompiledChange[] = []

  entitiesMap = new Map<string, Entity>()
  entities: Entity[] = []

  relationsMap = new Map<string, Relation>()
  relations: Relation[] = []

  get canUndo () {
    return this.changes.length > 0
  }

  get canRedo () {
    return this.undoneChanges.length > 0
  }

  reset () {
    while (this.canUndo) {
      this.undo()
    }

    this.changes = []
    this.undoneChanges = []
  }

  add (options: Omit<AdditionChange, 'type'>) {
    this.commitChange({
      type: 'addition',
      entities: options.entities,
      relations: options.relations,
    })
  }

  remove (options: Omit<RemovalChange, 'type'>) {
    this.commitChange({
      type: 'removal',
      entityIds: options.entityIds,
      relationIds: options.relationIds,
    })
  }

  edit (options: Omit<EditChange, 'type'>) {
    this.commitChange({
      type: 'edit',
      entities: options.entities,
      relations: options.relations,
    })
  }

  commitChange (change: Change, isInitial = false) {
    const compiledChange = this._compileChange(change, !isInitial)

    if (compiledChange.apply() == false) return

    if (!isInitial) {
      this.changes.push(compiledChange)
      this.undoneChanges = []
    }
  }

  undo () {
    const lastChange = this.changes.pop()
    if (!lastChange) return

    lastChange.revert()
    this.undoneChanges.push(lastChange)
    this.diagram.eventBus.emit({ name: 'undo' })
  }

  redo () {
    const lastUndoneChange = this.undoneChanges.pop()
    if (!lastUndoneChange) return

    lastUndoneChange.apply()
    this.changes.push(lastUndoneChange)
    this.diagram.eventBus.emit({ name: 'redo' })
  }

  parseEntities (entities: EntityDefinition[]) {
    const parsed: Entity[] = []
    for (const e of entities) {
      try {
        parsed.push(new Entity(this.diagram, e))
      } catch (err) {
        console.error('Could not parse entity. Entity:', e, 'Error:', err)
      }
    }
    return parsed
  }

  parseRelations (relations: RelationDefinition[]) {
    const parsed: Relation[] = []
    for (const r of relations) {
      try {
        parsed.push(new Relation(this.diagram, r))
      } catch (err) {
        console.error('Could not parse relation. Relation:', r, 'Error:', err)
      }
    }
    return parsed
  }

  private _compileChange (change: Change, trackEvents = true): CompiledChange {
    let applied = false
    let apply: (() => void | false)
    let revert: (() => void) | null = null

    switch (change.type) {
      case 'addition': {
        apply = () => {
          change.entities = change.entities?.filter(e => !this.entitiesMap.has(e.id))
          change.relations = change.relations?.filter(r => !this.relationsMap.has(r.id))

          const entities = this.parseEntities(change.entities ?? [])
          const relations = this.parseRelations(change.relations ?? [])

          // Cancel if nothing to add
          if (!entities.length && !relations.length) return false

          if (entities.length) this._addEntities(entities)
          if (relations.length) this._addRelations(relations)

          revert = () => {
            if (entities.length) this._removeEntities(entities)
            if (relations.length) this._removeRelations(relations)
          }
        }
        break
      }

      case 'removal': {
        apply = () => {
          const entities: Entity[] = []
          const relations: Relation[] = []

          for (const id of change.entityIds ?? []) {
            const entity = this.entitiesMap.get(id)
            if (entity) {
              entities.push(entity)
            }
          }

          for (const id of change.relationIds ?? []) {
            const relation = this.relationsMap.get(id)
            if (relation) {
              relations.push(relation)
            }
          }

          change.entityIds = change.entityIds && entities.map(e => e.id)
          change.relationIds = change.relationIds && relations.map(r => r.id)

          // Cancel if nothing to remove
          if (!entities.length && !relations.length) return false

          if (entities.length) this._removeEntities(entities)
          if (relations.length) this._removeRelations(relations)

          revert = () => {
            if (entities.length) this._addEntities(entities)
            if (relations.length) this._addRelations(relations)
          }
        }
        break
      }

      case 'edit': {
        apply = () => {
          // These will replace change.entities and change.relations, so unused edits are removed
          const entityEdits: EditChange['entities'] = change.entities && []
          const relationEdits: EditChange['relations'] = change.relations && []

          const reverters: (() => void)[] = []

          for (const edit of change.entities ?? []) {
            const entity = this.entitiesMap.get(edit.id)
            if (!entity) continue

            entityEdits?.push(edit)
            reverters.push(merge(entity, {
              data: edit.data ?? {},
              style: edit.style ?? {},
            }))
          }

          for (const edit of change.relations ?? []) {
            const relation = this.relationsMap.get(edit.id)
            if (!relation) continue

            relationEdits?.push(edit)
            reverters.push(merge(relation, {
              data: edit.data ?? {},
              style: edit.style ?? {},
            }))
          }

          change.entities = entityEdits
          change.relations = relationEdits

          // Cancel if nothing was edited
          if (!reverters.length) return false

          revert = () => reverters.forEach(revert => revert())
        }
        break
      }
    }

    return {
      change,
      apply: () => {
        if (applied) {
          console.warn('change.apply() was called after change was already applied')
          return
        }

        applied = true
        const result = apply()

        if (trackEvents) this.diagram.eventBus.emit({ name: 'dataUpdated' })

        return result
      },
      revert: () => {
        if (!revert) {
          console.warn('change.revert() was called before it was defined')
          return
        }

        if (!applied) {
          console.warn('change.revert() was called before change was applied')
          return
        }

        applied = false
        const result = revert()

        if (trackEvents) this.diagram.eventBus.emit({ name: 'dataUpdated' })

        return result
      },
    }
  }

  private _addEntities (entities: Entity[]) {
    for (const entity of entities) this.entitiesMap.set(entity.id, entity)
    this._updateEntitiesArray()
  }

  private _addRelations (relations: Relation[]) {
    for (const relation of relations) this.relationsMap.set(relation.id, relation)
    this._updateRelationsArray()
  }

  private _removeEntities (entities: Entity[]) {
    for (const entity of entities) {
      this.entitiesMap.delete(entity.id)
    }
    this._updateEntitiesArray()
  }

  private _removeRelations (relations: Relation[]) {
    for (const relation of relations) {
      this.relationsMap.delete(relation.id)
    }
    this._updateRelationsArray()
  }

  private _updateEntitiesArray () {
    this.entities = [...this.entitiesMap.values()]
  }

  private _updateRelationsArray () {
    this.relations = [...this.relationsMap.values()]
  }
}

export interface DiagramDataDefinition {
  entities?: EntityDefinition[]
  relations?: RelationDefinition[]
}

export interface CompiledChange {
  change: Change
  apply: () => void | false
  revert: () => void
}

export type Change = (
  | AdditionChange
  | RemovalChange
  | EditChange
)

export interface AdditionChange {
  type: 'addition'
  entities?: EntityDefinition[]
  relations?: RelationDefinition[]
}

export interface RemovalChange {
  type: 'removal'
  entityIds?: string[]
  relationIds?: string[]
}

export interface EditChange {
  type: 'edit'
  entities?: ({
    id: string
    data?: Record<string, any>
    style?: EntityStyle
  })[]
  relations?: ({
    id: string
    data?: Record<string, any>
    style?: RelationStyle
  })[]
}
